* {
  box-sizing: border-box;
}

body {
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  color: #212121;
  background-color: #fafafa;
  padding: 20px;
  font-family: -apple-system, BlinkMacSystemFont, Segoe UI, Roboto, Oxygen, Ubuntu, Cantarell, Open Sans, Helvetica Neue, sans-serif;
  font-size: 10px;
  line-height: 1.5;
}

.search-form {
  justify-content: center;
  margin-bottom: 20px;
  display: flex;
}

.search-form_input, .search-form_button {
  border-radius: 3px;
  padding: 5px;
}

.search-form_button {
  background-color: #f5f4fa;
  border-color: #f5f4fa;
  transition: background-color .25s cubic-bezier(.4, 0, .2, 1), border-color .25s cubic-bezier(.4, 0, .2, 1);
}

.search-form_button:hover {
  background-color: #e4e0f5;
  border-color: #e4e0f5;
}

.gallery {
  flex-wrap: wrap;
  margin: -5px;
  display: flex;
}

.photo-card {
  width: calc(25% - 10px);
  color: #000;
  border-radius: 5px;
  flex-direction: column;
  margin: 5px;
  text-decoration: none;
  display: flex;
  overflow: hidden;
  box-shadow: 0 1px 3px rgba(0, 0, 0, .12), 0 1px 1px rgba(0, 0, 0, .14), 0 2px 1px rgba(0, 0, 0, .2);
}

.photo-card_image {
  height: 200px;
  width: 350px;
  object-fit: cover;
  align-items: center;
  display: block;
}

.info {
  justify-content: space-around;
  padding-left: 5px;
  padding-right: 5px;
  display: flex;
}

.info-item {
  text-align: center;
  flex-direction: column;
  display: flex;
}

.info-item:not(:last-child) {
  margin-right: 5px;
}

.load-more {
  display: none;
}

.load-more.is-visible {
  display: block;
}

